.fund-progress {
    width: 100%;
    height: 1rem;

    margin-block: 1rem;

    -webkit-appearance: none;
    appearance: none;


    &::-webkit-progress-bar {
        background-color: var(--clr-neutral-300);
        border-radius: 2rem;
    }

    &::-webkit-progress-value {
        background-color: var(--clr-primary-400);
        border-radius: 2rem;
    }
}
